.todoListMain .header input {
    padding: 15px;
    font-size: 20px;
    width: 165px;
    border: solid white;
    border-radius:5px;
}

.todoListMain .header button {
    padding: 15px;
    font-size: 20px;
    margin: 10px;
    margin-right: 0px;
    background-color:cornflowerblue;
    color: white;
    border:2px solid cornflowerblue;
    border-radius:5px;
}

.todoListMain .header button:hover {
    background-color:blue;
    border: 2px solid blue;
    cursor: pointer;
}

.todoListMain .itemList {
    list-style: none;
    padding-left: 0;
    width: 279px;
} 

.todoListMain .itemList li {
    color: black;
    background-color:white;
    padding: 15px;
    margin-bottom: 15px;
    border-radius: 5px;
    list-style: none;
    transition: background-color .2s ease-out;
}

.todoListMain .itemList li:hover{
    background-color:darksalmon;
    cursor:pointer;
}

ul.theList {
    padding: 0;
}

@media and screen (max-width:600px){
    .todoListMain .header input{
        width:100px;
    }

}